import React, { Component } from 'react'
//import PropTypes from 'prop-types'
import 'antd/dist/antd.css';

import { Upload, Icon, Button, Progress, Modal, Spin, message } from 'antd'

import request from 'superagent'
import SparkMD5 from 'spark-md5'

const confirm = Modal.confirm
const Dragger = Upload.Dragger

class FileUpload extends Component {
  constructor(props) {
    super(props)
    this.state = {
      preUploading: false,   //
      chunksSize: 0,   // 
      currentChunks: 0,  //  
      uploadPercent: -1,  // 
      preUploadPercent: -1, //   
      uploadRequest: false, // 
      uploaded: false, // 
      uploading: false, // 
    }
  }
  showConfirm = () => {
    const _this = this
    confirm({
      title: '?',
      content: '',
      onOk() {
        _this.preUpload()
      },
      onCancel() { },
    })
  }


  preUpload = () => {//
    //let res = request.post('http://localhost:3000', this.state.uploadParams)
    //console.log(res);
    var Console = console;
    let uploadList = this.state.uploadParams.chunks//
    Console.log(this.state.uploadParams)
    let currentChunks = this.state.uploadParams.file.fileChunks

    var _this = this
    // 
    let uploadPercent = Number(((this.state.chunksSize - currentChunks) / this.state.chunksSize * 100).toFixed(2))
    // 
    if (uploadPercent === 100) {
      message.success('')
      _this.setState({
        uploaded: true,    // 
        uploading: false
      })
    } else {
      _this.setState({
        uploaded: false,
        uploading: true
      })
    }

    _this.setState({
      uploadRequest: false,    // 
      currentChunks: currentChunks,
      uploadPercent
    })

    //
    this.handlePartUpload(uploadList)
  }

  handlePartUpload = (uploadList) => {
    //let blobSlice = File.prototype.slice || File.prototype.mozSlice || File.prototype.webkitSlice
    const _this = this
    const batchSize = 1,    // 
      total = uploadList.length,   //
      batchCount = total / batchSize    // 
    let batchDone = 0     //
    doBatchAppend()
    function doBatchAppend() {
      if (batchDone < batchCount) {
        let list = uploadList.slice(batchSize * batchDone, batchSize * (batchDone + 1))
        setTimeout(() => silcePart(list), 2000);
      }
    }

    function silcePart(list) {
      var Console = console
      batchDone += 1;
      doBatchAppend();
      list.forEach((value) => {
        let { fileMd5, chunkMd5, chunk, start, end } = value
        let formData = new FormData(),
          blob = new Blob([_this.state.arrayBufferData[chunk - 1].currentBuffer], { type: 'application/octet-stream' }),
          params = `fileMd5=${fileMd5}&chunkMd5=${chunkMd5}&chunk=${chunk}&start=${start}&end=${end}&chunks=${_this.state.arrayBufferData.length}`

        const chunk_info = '{"subscript":"5","chunk":"5","hash":"81cc181194afc67fa7c52a88e57c2e52","username":"lyp970805@163.com"}'
        formData.append('chunk_info', chunk_info)
        formData.append('chunk', blob, chunkMd5)
        request
          .post('http://120.55.41.240:20521')
          .send(formData)
          .withCredentials()
          .retry(2)
          .end((err, res) => {
            Console.log(res);
            if (err) {
              alert('')
            }
            if (res.statusCode === 200) {
              let currentChunks = _this.state.currentChunks
              --currentChunks
              // 
              let uploadPercent = Number(((_this.state.chunksSize - currentChunks) / _this.state.chunksSize * 100).toFixed(2))
              _this.setState({
                currentChunks,  // chunks
                uploadPercent,
                uploading: true
              })
              if (currentChunks === 0) {
                // api

                //_this.checkUploadStatus(_this.state.fileMd5)
                _this.setState({
                  uploading: false,
                  uploaded: true
                })
              }
            }
          })
      })
    }
  }
  render() {
    const { preUploading, uploadPercent, preUploadPercent, uploadRequest, uploaded, uploading } = this.state
    const _this = this
    const uploadProp = {
      onRemove: (file) => {
        _this.setState(({ fileList }) => {
          const index = fileList.indexOf(file)
          const newFileList = fileList.slice()
          newFileList.splice(index, 1)
          return {
            fileList: newFileList,
          }
        })
      },
      beforeUpload: (file) => {
        // 
        _this.setState({
          uploaded: false,   // 
          uploading: false,  // 
          uploadRequest: false   // 
        })
        // 
        let blobSlice = File.prototype.slice || File.prototype.mozSlice || File.prototype.webkitSlice,
          chunkSize = 1024 * 5,                             // 5M
          chunks = Math.ceil(file.size / chunkSize),  //
          currentChunk = 0, // chunk
          spark = new SparkMD5.ArrayBuffer(),
          // arrayBuffermd5md5
          chunkFileReader = new FileReader(),  // chunkMd5
          totalFileReader = new FileReader()  // fileMd5

        let params = { chunks: [], file: {} },   // md5
          arrayBufferData = []              // chunkarrayBuffer,
        params.file.fileName = file.name
        params.file.fileSize = file.size

        totalFileReader.readAsArrayBuffer(file)
        totalFileReader.onload = function (e) {
          // totalFilemd5
          spark.append(e.target.result)
          params.file.fileMd5 = spark.end() // fileMd5
        }

        chunkFileReader.onload = function (e) {
          // md5
          spark.append(e.target.result)
          // 
          let obj = {
            chunk: currentChunk + 1,
            start: currentChunk * chunkSize, // 
            end: ((currentChunk * chunkSize + chunkSize) >= file.size) ? file.size : currentChunk * chunkSize + chunkSize, // 
            chunkMd5: spark.end(),
            chunks
          }
          // onload,currentChunk
          currentChunk++;
          params.chunks.push(obj)

          // arrayBufferpartUpload
          let tmp = {
            chunk: obj.chunk,
            currentBuffer: e.target.result
          }
          arrayBufferData.push(tmp)

          if (currentChunk < chunks) {
            // 
            loadNext()

            // 
            _this.setState({
              preUploading: true,
              preUploadPercent: Number((currentChunk / chunks * 100).toFixed(2))
            })
          } else {
            //chunks
            params.file.fileChunks = params.chunks.length
            //console.log(params)
            // arrayBuffer
            _this.setState({
              preUploading: false,
              uploadParams: params,
              arrayBufferData,
              chunksSize: chunks,
              preUploadPercent: 100
            })


          }
        }

        chunkFileReader.onerror = function () {
          var Console = console
          Console.warn('oops, something went wrong.');
        };

        function loadNext() {
          var start = currentChunk * chunkSize,
            end = ((start + chunkSize) >= file.size) ? file.size : start + chunkSize;
          chunkFileReader.readAsArrayBuffer(blobSlice.call(file, start, end));
        }

        loadNext()

        // 
        _this.setState({
          fileList: [file],
          file: file
        })
        return false
      },
      //action: 'http://120.55.41.240:20521',
      fileList: _this.state.fileList,
    }



    return (
      <div className="content-inner">
        <Spin
          spinning={preUploading}
          style={{ height: 350 }}
          tip={
            <div >
              <h3
                style={{ margin: '10px auto', color: '#1890ff' }}
              >
                ...
              </h3>
              <Progress
                percent={preUploadPercent}
                status="active"
                type="circle"
                width={80}
              />
            </div>
          }
        >
          <div style={{ marginTop: 16, height: 250 }}>
            <Dragger {...uploadProp}>
              <p className="ant-upload-drag-icon">
                <Icon type="inbox" />
              </p>
              <p className="ant-upload-text"></p>
              <p className="ant-upload-hint">Support for a single or bulk upload. Strictly prohibit from uploading company data or other band files</p>
            </Dragger>
            {uploadPercent >= 0 && !!uploading && <div style={{ marginTop: 20, width: '95%' }}>
              <Progress
                percent={uploadPercent}
                status="active"
              />
              <h4></h4>
            </div>}
            {!!uploadRequest && <h4 style={{ color: '#1890ff' }}>...</h4>}
            {!!uploaded && <h4 style={{ color: '#52c41a' }}></h4>}
            <Button
              disabled={!!(this.state.preUploadPercent < 100)}
              onClick={this.showConfirm}
              type="primary"
            >
              <Icon type="upload" />
            </Button>
          </div>
        </Spin>
      </div>
    )
  }
}

FileUpload.propTypes = {
  //...
}

export default FileUpload;